import { Button } from '../Controls'
import { PickerStore } from './store'
import SourceDialog from './KeymapSourceDialog'
import { useModalState } from '../../Common/Modal'

export default function ClipboardPicker ({ onSelect, onError }) {
  const [keymapModalOpen, openKeymapModal, closeKeymapModal] = useModalState()
  // const [layoutModalOpen, openlayoutModal, closeLayoutModal] = useModalState()

  return (
    <PickerStore>
      <Button onClick={openKeymapModal}>Keymap</Button>
      <Button>Layout</Button>
      {keymapModalOpen && (
        <SourceDialog
          onSubmit={closeKeymapModal}
          onCancel={closeKeymapModal}
        />
      )}
    </PickerStore>
  )
}
