import { useCallback, useContext, useState } from 'react'

import Modal from '../../Common/Modal'
import DialogBox from '../../Common/DialogBox'
import ActionButton from '../../Common/ActionButton'
import { ClipboardContext } from './store'

export default function KeymapSourceDialog ({ onSubmit, onCancel }) {
  const [{ keymapSource }, { setKeymap }] = useContext(ClipboardContext)
  const [content, setContent] = useState(keymapSource)

  const handleChange = useCallback(event => setContent(event.target.value), [setContent])

  const handleSubmit = useCallback(() => {
    setKeymap(content)
    onSubmit(content)
  }, [content, setKeymap, onSubmit])

  return (
    <Modal onDismiss={onCancel}>
      <DialogBox style={{ maxWidth: 'fit-content' }} dismissText={null}>
        <h2>Set Keymap</h2>
        <p>Paste the devicetree source from your <code>.keymap</code> file below.</p>
        <textarea
          cols={80}
          rows={25}
          wrap="off"
          value={content}
          onChange={handleChange}
        />
        <p style={{ textAlign: 'center' }}>
          <ActionButton onClick={handleSubmit}>Load</ActionButton>
          <ActionButton onClick={onCancel} secondary>Close</ActionButton>
        </p>
      </DialogBox>
    </Modal>
  )
}
