const INITIAL_STATE = {
  keymapSource: INITIAL_KEYMAP,
  keymapParsed: null,
  metadataSource: INITIAL_METADATA,
  metadataParsed: null
}

export const pickerSlice = createSlice({
  name: 'clipboard-picker',
  initialState: INITIAL_STATE,
  reducers: {
    setKeymap (state, action) {
      const { source, parsed } = action.payload
      state.keymapSource = source
      state.keymapParsed = parsed
    },
    setMetadataSource (state, action) {}
  }
})

function useActionDispatchers (dispatch) {
  return useMemo(() => (
    Object.keys(pickerSlice.actions).reduce((actions, action) => {
      actions[action] = (...args) => dispatch(pickerSlice.actions[action](...args))
      return actions
    }, {})
  ), [dispatch])
}

export function usePickerStore () {
  const [state, dispatch] = useReducer(pickerSlice.reducer, INITIAL_STATE)
  const dispatchers = useActionDispatchers(dispatch)

  return [state, dispatchers]
}

export const ClipboardContext = createContext([{}, {}])

export function PickerStore ({ children }) {
  const [state, dispatchers] = usePickerStore()

  return (
    <ClipboardContext.Provider value={[state, dispatchers]}>
      {children}
    </ClipboardContext.Provider>
  )
}
