import find from 'lodash/find'
import get from 'lodash/get'

export function getBehaviourParams (parsedParams, behaviour) {
  return [].concat(
    behaviour.params || [],
    getAdditionalParams(parsedParams, behaviour)
  )
}

function getAdditionalParams (parsedParams, behavior) {
  const firstParsedParam = get(parsedParams, '[0]', {})

  if (get(behavior, 'params[0].enum')) {
    const source = find(behavior.params[0].enum, { code: firstParsedParam.value })
    if (source?.additionalParams) {
      return source.additionalParams
    }
  }

  return []
}
